import React, { useEffect, useRef } from "react";
import "./styles.css";

export default function App() {
  const wrapper = useRef();

  useEffect(() => {
    const bot = new window.ChatSDK({
      root: wrapper.current,
      config: {
        navbar: {
          title: "ChatGPT"
        },
        robot: {
          avatar: "https://mspoweruser.com/wp-content/webp-express/webp-images/uploads/2020/09/Microsoft-OpenAI-GPT-3.jpg.webp"
        },
        messages: [
          {
            type: "text",
            content: {
              text: "智能助理为您服务，请问有什么可以帮您？"
            }
          }
        ]
      },
      requests: {
        /* ... */
      },
      handlers: {
        /* ... */
      }
    });
    bot.run();
  }, []);

  return <div style={{ height: "100%" }} ref={wrapper} />;
}
